.collections{text-align:center;margin-bottom:80px}.collections .tabs{display:flex;flex-wrap:wrap;justify-content:center;gap:24px;margin:16px 0}.collections .tabs button{font-size:20px;line-height:48px;padding:0px 24px;background:none;border:none;cursor:pointer}.collections .tabs button:first-child{background-image:linear-gradient(45deg,#ffffff,#000000);-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-clip:text;text-emphasis-color:transparent}.collections .tabs button:last-child{background-image:linear-gradient(45deg,#000000,#ffffff);-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-clip:text;text-emphasis-color:transparent}.collections .tabs button:hover{color:#C6A26D;-webkit-background-clip:initial;-webkit-text-fill-color:initial;background-clip:initial;background-image:none}.collections .banner-layout{display:flex;justify-content:center}.collections .banner-layout .grid{display:grid;grid-template-columns:repeat(auto-fit,minmax(180px,1fr));gap:16px;width:1200px}.collections .banner-layout .grid .grid-item{grid-column:span 2}.collections .banner-layout .grid .grid-item:nth-child(3n+1){grid-column:span 3}.collections .banner-layout .grid .grid-item.grid-image{background-size:cover;background-position:center;position:relative;min-height:200px;display:flex;align-items:center;justify-content:flex-end;padding:0 64px}.collections .banner-layout .grid .grid-item.grid-image .grid-image-content{text-align:center;width:70%}.collections .banner-layout .grid .grid-item.grid-image .grid-image-content .title{font-size:56px;line-height:64px;color:#ffffff}.collections .banner-layout .grid .grid-item.grid-image .grid-image-content .subtitle{font-size:14px;line-height:18px;color:#C6A26D}.collections .banner-layout .grid .grid-item.grid-image .grid-image-content .description{font-size:12px;line-height:16px;color:#ffffff;margin-top:32px;margin-bottom:24px}.collections .banner-layout .grid img{width:100%;object-fit:cover;height:437px}